// Core
import React, { Component } from 'react';
import {
    Transition,
    CSSTransition,
    TransitionGroup
} from 'react-transition-group';
import gsap from 'gsap';

// Instruments
import Styles from './styles.m.css';
import { api, TOKEN, GROUP_ID } from 'config';
import { withProfile } from 'components/HOC/withProfile';
import { socket } from 'socket/init';

// Components
import StatusBar from 'components/StatusBar';
import Composer from 'components/Composer';
import Post from 'components/Post';
import Catcher from 'components/Catcher';
import Counter from 'components/Counter';
import Spinner from 'components/Spinner';
import Postman from 'components/Postman';

@withProfile
export default class Feed extends Component {
    state = {
        posts:           [],
        isPostsFetching: false,
    };

    componentDidMount () {
        const { currentUserFirstName, currentUserLastName } = this.props;

        this._fetchPosts();
        socket.emit('join', GROUP_ID);

        socket.on('create', (postJSON) => {
            const { data: createdPost, meta } = JSON.parse(postJSON);

            if (
                `${currentUserFirstName} ${currentUserLastName}` !==
                `${meta.authorFirstName} ${meta.authorLastName}`
            ) {
                this.setState(({ posts }) => ({
                    posts: [createdPost, ...posts],
                }));
            }
        });

        socket.on('remove', (postJSON) => {
            const { data: removedPost, meta } = JSON.parse(postJSON);

            if (
                `${currentUserFirstName} ${currentUserLastName}` !==
                `${meta.authorFirstName} ${meta.authorLastName}`
            ) {
                this.setState(({ posts }) => ({
                    posts: posts.filter((post) => post.id !== removedPost.id),
                }));
            }
        });

        socket.on('like', (postJSON) => {
            const { data: likedPost, meta } = JSON.parse(postJSON);

            if (
                `${currentUserFirstName} ${currentUserLastName}` !==
                `${meta.authorFirstName} ${meta.authorLastName}`
            ) {
                this.setState(({ posts }) => ({
                    posts: posts.map(
                        (post) => post.id === likedPost.id ? likedPost : post
                    ),
                }));
            }
        });
    }

    componentWillUnmount () {
        clearInterval(this.refetch);
    }

    _setPostsFetchingState = (state) => {
        this.setState({
            isPostsFetching: state,
        });
    };

    _createPost = async ({ comment }) => {
        try {
            this._setPostsFetchingState(true);

            const response = await fetch(api, {
                method:  'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization:  TOKEN,
                },
                body: JSON.stringify({
                    comment,
                }),
            });

            if (response.status !== 200) {
                this._setPostsFetchingState(false);
                throw new Error('Post was not created!');
            }

            const { data: post } = await response.json();

            this.setState(({ posts }) => ({
                posts:           [post, ...posts],
                isPostsFetching: false,
            }));
        } catch ({ message }) {
            console.log(message);
        }
    };

    _fetchPosts = async () => {
        try {
            this._setPostsFetchingState(true);

            const response = await fetch(api, {
                method: 'GET',
            });

            if (response.status !== 200) {
                this._setPostsFetchingState(false);
                throw new Error('Posts were not loaded.');
            }

            const { data: posts } = await response.json();

            this.setState(() => ({ posts, isPostsFetching: false }));
        } catch ({ message }) {
            console.log(message);
        }
    };

    _removePost = async (id) => {
        try {
            this._setPostsFetchingState(true);

            const response = await fetch(`${api}/${id}`, {
                method:  'DELETE',
                headers: {
                    Authorization: TOKEN,
                },
            });

            if (response.status !== 204) {
                this._setPostsFetchingState(false);
                throw new Error('Post was not deleted!');
            }

            this.setState(({ posts }) => ({
                posts:           posts.filter((post) => post.id !== id),
                isPostsFetching: false,
            }));
        } catch ({ message }) {
            console.log(message);
        }
    };

    _likePost = async (id) => {
        try {
            this._setPostsFetchingState(true);

            const response = await fetch(`${api}/${id}`, {
                method:  'PUT',
                headers: {
                    Authorization: TOKEN,
                },
            });

            if (response.status !== 200) {
                this._setPostsFetchingState(false);
                throw new Error('Post was not liked!');
            }

            const { data } = await response.json();

            this.setState(({ posts }) => ({
                posts:           posts.map((post) => post.id === data.id ? data : post),
                isPostsFetching: false,
            }));
        } catch ({ message }) {
            console.log(message);
        }
    };

    _handleComposerAppear = (composer) => {
        gsap.fromTo(composer, 1, { opacity: 0 }, { opacity: 1 });
    };

    _handleCounterAppear = (counter) => {
        gsap.fromTo(counter, 1, { opacity: 0 }, { opacity: 1 });
    };

    _handlePostmanAppear = (postman) => {
        gsap.fromTo(postman, 1, { x: 400, opacity: 0 }, { x: 0, opacity: 1 });
    };

    _handlePostmanDisappear = (postman) => {
        gsap.fromTo(postman, 1, { x: 0, opacity: 1 }, { x: 400, opacity: 0 });
    };

    render () {
        const { posts, isPostsFetching } = this.state;

        const postsJSX = posts.map(
            ({ avatar, comment, created, firstName, id, lastName, likes }) => (
                <CSSTransition
                    classNames = { {
                        enter:       Styles.postInStart,
                        enterActive: Styles.postInEnd,
                        exit:        Styles.postOutStart,
                        exitActive:  Styles.postOutEnd,
                    } }
                    key = { id }
                    timeout = { { enter: 500, exit: 400 } }>
                    <Catcher>
                        <Post
                            _likePost = { this._likePost }
                            _removePost = { this._removePost }
                            avatar = { avatar }
                            comment = { comment }
                            created = { created }
                            firstName = { firstName }
                            id = { id }
                            lastName = { lastName }
                            likes = { likes }
                        />
                    </Catcher>
                </CSSTransition>
            )
        );

        return (
            <section className = { Styles.feed }>
                <Spinner isSpinning = { isPostsFetching } />
                <StatusBar />
                <Transition
                    appear
                    in
                    timeout = { 1000 }
                    onEnter = { this._handleComposerAppear }>
                    <Composer _createPost = { this._createPost } />
                </Transition>
                <Transition
                    appear
                    in
                    timeout = { 1000 }
                    onEnter = { this._handleCounterAppear }>
                    <Counter count = { posts.length } />
                </Transition>
                <TransitionGroup>{postsJSX}</TransitionGroup>
                <Transition
                    appear
                    in
                    timeout = { 3000 }
                    onEnter = { this._handlePostmanAppear }
                    onEntered = { this._handlePostmanDisappear }>
                    <Postman />
                </Transition>
            </section>
        );
    }
}
