// Core
import React, { Component } from 'react';
import { string, func, number, arrayOf, shape } from 'prop-types';
import moment from 'moment';

// Instruments
import Styles from './styles.m.css';

// Components
import Like from 'components/Like';
import { Consumer } from 'components/HOC/withProfile';

export default class Post extends Component {
    static propTypes = {
        _likePost:   func.isRequired,
        _removePost: func.isRequired,
        avatar:      string.isRequired,
        comment:     string.isRequired,
        created:     number.isRequired,
        firstName:   string.isRequired,
        id:          string.isRequired,
        lastName:    string.isRequired,
        likes:       arrayOf(
            shape({
                firstName: string.isRequired,
                lastName:  string.isRequired,
            }).isRequired
        ).isRequired,
    };

    _removePost = () => {
        const { _removePost, id } = this.props;

        _removePost(id);
    };

    _getCross = (context) => {
        const { firstName, lastName } = this.props;

        return `${firstName} ${lastName}` ===
            `${context.currentUserFirstName} ${context.currentUserLastName}` ? (
                <span className = { Styles.cross } onClick = { this._removePost } />
            ) : null;
    };

    render () {
        const {
            avatar,
            comment,
            created,
            firstName,
            id,
            lastName,
            likes,
            _likePost,
        } = this.props;

        return (
            <Consumer>
                {(context) => (
                    <section className = { Styles.post }>
                        {this._getCross(context)}
                        <img src = { avatar } />
                        <a>{`${firstName} ${lastName}`}</a>
                        <time>
                            {moment.unix(created).format('MMMM D h:mm:ss a')}
                        </time>
                        <p>{comment}</p>
                        <Like _likePost = { _likePost } id = { id } likes = { likes } />
                    </section>
                )}
            </Consumer>
        );
    }
}
