// Core
import React, { Component } from 'react';
import { func } from 'prop-types';

// Instruments
import Styles from './styles.m.css';
import { withProfile } from 'components/HOC/withProfile';

export class Composer extends Component {
    static propTypes = {
        _createPost: func.isRequired,
    };

    state = {
        comment: '',
    };

    _handleSubmit = (event) => {
        event.preventDefault();
        this._createPost();
    };

    _createPost = () => {
        const { comment } = this.state;

        if (!comment) {
            return;
        }

        this.props._createPost({ comment });

        this.setState(() => ({
            comment: '',
        }));
    };

    _handleTextareaChange = (event) => {
        const { value: comment } = event.target;

        this.setState(() => ({ comment }));
    };

    _handleTextareaCopy = (event) => {
        event.preventDefault();
    };

    _handleTextareaKeyPress = (event) => {
        const enterKey = event.key === 'Enter';

        enterKey ? this._createPost() : null;

        if (enterKey) {
            event.preventDefault();
        }
    };

    render () {
        const { avatar, currentUserFirstName } = this.props;
        const { comment } = this.state;

        return (
            <section className = { Styles.composer }>
                <img src = { avatar } />
                <form onSubmit = { this._handleSubmit }>
                    <textarea
                        placeholder = { `What's on your mind, ${currentUserFirstName}?` }
                        value = { comment }
                        onChange = { this._handleTextareaChange }
                        onCopy = { this._handleTextareaCopy }
                        onKeyPress = { this._handleTextareaKeyPress }
                    />
                    <input type = 'submit' value = 'Post' />
                </form>
            </section>
        );
    }
}

export default withProfile(Composer);
